html,body {
  width: 100%;
  height: 100%;
  background: #f4f5f6;
  font-family: "Roboto", "Helvetica Neue", Arial, sans-serif;
}

#root {
  width: 100%;
  height: 100%;
}

.code {
  font-family: 'Courier New';
}

.border-bottom {
  border-bottom: 1px solid #eee;
}

a:focus {
  outline: 0;
  outline-width: 0px
}

a {
  color: #333;
  outline: 0;
  text-decoration: none;
  cursor:pointer;
}

a:focus {
  box-shadow: 0 0 0 .1rem rgba(87,85,217,.2)
}

a.active,a:active,a:focus,a:hover {
  color: #406599;
  text-decoration: underline
}

a:visited {
  color: #333
}

.customform {
  padding:1rem 1rem 1rem 2rem;
  background:#303742!important;
  display:block;
}

.text-v {
  color: orange;
}

.form-input::placeholder {
  color: #aaa;
  font-size: 0.8em;
}

.has-icon-left .form-icon, .has-icon-right .form-icon {
  height: auto;
  margin: 0 .25rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width:auto;
  line-height: 1;
  z-index: 2;
  font-size: 1.2em;
}

.no-background {
  background: none !important;
}

.no-border {
  border:none !important;
}

.no-shadow {
  box-shadow: none !important;
}

.flex-wrap {
  flex-wrap: wrap;
}

.p-1rem {
  padding: 1rem;
}
.pl-1rem {
  padding-left: 1rem;
}
.pr-1rem {
  padding-right: 1rem;
}

.p-10px {
  padding: 10px;
}

.w-2 {
  width: 2rem;
}
.w-3 {
  width: 3rem;
}
.w-4 {
  width: 4rem;
}
.w-5 {
  width: 5rem;
}
.w-6 {
  width: 6rem;
}
.w-7 {
  width: 7rem;
}
.w-8 {
  width: 8rem;
}
.w-9 {
  width: 9rem;
}
.w-10 {
  width: 10rem;
}
.w-12 {
  width: 12rem;
}

.lh-1 {
  line-height:1;
}
.lh-1rem {
  line-height: 1rem;
}
.lh-2rem {
  line-height:2rem;
}

.font-bold {
  font-weight: bold;
}
.font-lighter {
  font-weight: lighter;
}

.font-size-08 {
  font-size:0.8em;
}
.font-size-1 {
  font-size: 1em;
}
.font-size-1_2 {
  font-size: 1.2em;
}
.font-size-2 {
  font-size: 2em;
}
.font-size-3 {
  font-size: 3em;
}
.font-size-4 {
  font-size: 4em;
}
a.icon-text {
  cursor: pointer;
  text-decoration: none;
}
.icon-text {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-weight: lighter;
  margin-right: 0.4rem;
}
.icon-text-icon {
  font-size: 1.2em;
}
.icon-text-text {
  font-size: 0.9em;
}

.icon-text.active {
  color: #ff1729;
}

.color-red {
  color: #ff1729;
}

.color-green {
  color:#00dd1c;
}

.color-white {
  color:#ffffff;
}

.ads {
  background: #efefef;
  margin:0 0 1rem 0;
  min-height: 6rem;
}

.mb-1rem {
  margin-bottom:1rem;
}
.mt-1rem {
  margin-top:1rem;
}
.ml-1rem {
  margin-left:1rem;
}
.mr-1rem {
  margin-right:1rem;
}
.mt-10px {
  margin-top:10px;
}
.mb-10px {
  margin-bottom:10px;
}

.mt-5px {
  margin-top: 5px;
}

.mb-5px {
  margin-bottom: 5px;
}

.pb-5px {
  padding-bottom: 5px !important;
}

.plr-05rem {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.flex-1 {
  flex:1
}

.flex-no-1{
  flex: auto;
}

.box-title {
  font-size: 1.2em;
}

.text-tips {
  font-size: 0.9em;
  color: #777777;
  font-weight: lighter;
  padding: 0.2rem 0;
}

.flex-row-between {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.flex-row-between-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.flex-row-around {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.flex-row-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.flex-row-left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.flex-row-left-top {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.flex-row-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.flex-column-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.column-center-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.row-center-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.no-sticky {
  position: relative !important;
  top: 0 !important;
}

.inner-page {
  background: #f1f5f9;
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.table-border {
  border-left:1px solid #ddd;
  border-top:1px solid #ddd;
}
.table-border td {
  border-right:1px solid #ddd;
  border-bottom:1px solid #ddd;
  vertical-align: top;
}

.table-border .td {
  border-right:1px solid #ddd;
  border-bottom:1px solid #ddd;
  vertical-align: top;
  padding:1rem;
  width:6.4rem;
  min-height: 6rem;
}

.modal-container .modal-header {
  padding-bottom: 0;
}

.gap-line {
  width: 100%;
  border-bottom: 1px solid #ddd;
  margin: 1rem 0;
}

.loading-more {
  display: block;
  text-align: center;
  padding: 0.4rem 0;
  font-weight: lighter;
  color: #777777;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 0.9em;
}
.loading-more a {
  text-decoration: none;
  padding: 0.2rem 1rem;
  border: 1px solid #ddd;
  font-size: 1em;
  color:#333;
  font-weight: bold;
}
.loading-more a:active {
  background:#ff1729;
  color: #fff;
}

.loading-more .loading {
  margin-right: 1rem;
}
.loading-error a {
  color: #ff1729;
}



#mainWrap {
  width: 100%;
  margin:1rem auto 0 auto;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex:1;
}


#mainContent {
  flex:1;
  margin: 0rem;
  height: 100%;
  overflow-y: auto;
}



/*S 空内容提示*/
.empty-icon .icon {
  font-size: 6em;
}
/*E 空内容提示*/



/*S loadingCover*/
#loadingCover {
  position: fixed;
  left: 0rem;
  right: 0rem;
  top: 0rem;
  bottom: 0rem;
  z-index: 1000000;
}

#loadingCover .loading-overlay {
  width:100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background:rgba(255,255,255,0.6) ;
}
/*E loadingCover*/

/*S toastsPanel*/
#toastsPanel {
  position: fixed;
  top: 5%;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  z-index: 10000000;
}
#toastsPanel .toast{
  max-width: 30rem;
  display: flex;
  margin-bottom: 0.1rem;
  justify-content: flex-start;
  align-items: center;
}
#toastsPanel .toast .icon {
  margin-right: 0.4rem;
  color: #fff;
  font-weight: bold;
  font-size: 2em;
  line-height: 1;
}
/*E toastsPanel*/

.page-wrap {
  background: #fff;
  max-width: 80rem;
  margin: 0 auto;
  padding: 1rem;
}

.CodeMirror {
  border-radius: 0px !important;
}
