.playbook {

}

.playbook-pane {
    min-height: calc(100vh - 11rem) !important;
    padding: 0 1rem !important;
}

.playbook-item-intro {
    height:calc(100vh - 17rem) !important;
    overflow: hidden;
    overflow-y: auto;
}
.playbook-item-run-script {
    background: #fffaf3;
    height: 6rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 1rem;
    box-shadow: 0 0 0 1px #c9ba9b inset, 0 0 0 0 transparent;
}
.playbook-item-run-script .field {
    width: 100%;
}
