.top-header {
    position: relative;
    z-index: 1000;
    height: 52px;
    padding: 0 16px 0 0px;
    background: #ffffff;
    border-bottom: 0px;
    box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.sidemenu {
    position: fixed;
    display: flex;
    flex-flow: column;
    flex-direction: column;
    width: auto;
    z-index: 1020;
    height: auto;
    top: 0px;
    box-shadow: 4px 4px 12px -8px #52545c;
    background: #ffffff;
}

.sidemenu__logo {
    box-shadow: 4px 4px 12px -8px #52545c;
    background: #ffffff;
    padding: 0px 0px 0px 0px;
    min-height: 52px;
    height: 52px;
    top: 0px;
    margin-right: 1rem;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    display: flex;
}

.sidemenu__logo .menu-img {
    height: 16px;
    margin-left: 8px;
    margin-right: 8px;
}
.sidemenu__logo .icon-menu {
    background: #2680eb;
    color: #ffffff;
    width: 52px;
    height: 52px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.sidemenu__logo .icon-menu img {
    height: 32px;
}

.top-header-menu {
    margin: 0 !important;
    flex:1;
}
